import { Pie } from "@ant-design/plots";
import { Card, Col, Row, Typography } from "antd";

const { Title } = Typography;
const CPieChartJenisBarang = ({ data = [] }) => {
  const config = {
    data: data.reverse(),
    angleField: "total",
    colorField: "name",
    radius: 0.9,
    tooltip: {
      formatter: (item) => ({
        name: item.name,
        value: item.total.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, "$&,"),
      }),
    },
    label: {
      type: "inner",
      content: () => "",
      style: {
        fontSize: 16,
        textAlign: "center",
      },
    },

    interactions: [
      {
        type: "element-selected",
      },
      {
        type: "element-active",
      },
    ],
  };

  return (
    <Col span={8}>
      <Card>
        <Row style={{ justifyContent: "center" }}>
          <Col span={24} style={{ textAlign: "center" }}>
            <Title level={4}>Total Asset By Jenis Barang</Title>
            <Pie {...config} />
          </Col>
        </Row>
      </Card>
    </Col>
  );
};

export default CPieChartJenisBarang;
